.flexed {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.rowBox {
  width: 90vw;
  height: fit-content;
  margin: 0 auto;
  border: 1px solid var(--black);
  border-radius: 6px;
  margin-bottom: 1rem;
}

.rowBoxCol {
  align-self: center;
}

.previews {
  width: 50vw;
  margin-bottom: auto;
}

@media screen and (max-width: 900px) {
  h4 {
    font-size: 1rem;
  }
  .row {
    flex-direction: column;
    margin-bottom: 2rem;
  }
  .rowBoxCol {
    width: 100%;
  }

  .rowBoxCol:last-child {
    margin-top: 1rem;
  }
}
