.link {
  color: var(--main-color);
  text-decoration: none;
}

.companyTitle {
  text-align: center;
  font-size: 6.5rem;
  font-weight: 700;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}

h4 {
  font-size: 1rem;
  margin-top: 1rem;
}

/* UTILITIES */
.container {
  width: 95vw;
}

.headerImg {
  display: block;
  margin: 0 auto;
  margin-bottom: 1.5rem;
  width: 55vw;
  border-radius: 1rem;
}

.subImg {
  display: block;
  margin: 0 auto;
  margin-bottom: 1.5rem;
  width: 50vw;
  border-radius: 1rem;
}

.flexed {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: auto 5vw;
}

.leftBar {
  border-left: 6px inset;
  margin: 0.5%;
  background: lightgrey;
  padding-right: 0.5%;
  width: fit-content;
}

section h5 {
  padding-left: 0.5%;
  font-weight: 100;
}

.inProgress {
  display: inline-block;
  margin: 0 auto;
  height: 50px;
  border: 1px solid var(--main-color);
  color: var(--main-color);
  padding-left: 20px;
  padding-right: 20px;
  line-height: 50px;
}

.design {
  width: 35rem;
  height: auto;
  max-width: 90vw;
  margin-bottom: 3rem;
}

@media screen and (max-width: 500px) {
  .companyTitle {
    font-size: 3.5rem;
  }
  .headerImg {
    width: 75vw;
  }
}
