h1 {
  text-align: center;
  font-size: 5.4rem;
  padding-bottom: 0.75rem;
}
h2 {
  font-size: 1.5rem;
  transition-duration: 0.5s;
}
.job {
  color: var(--border-grey);
}

.container {
  width: 100vw;
  align-items: flex-start;
  margin: 0 auto;
  padding: 2rem;
  max-width: 100%;
}

.section {
  max-height: 50vh;
  display: flex;
  align-items: flex-start;
}
.col1 {
  width: 30vw;
  flex: 2;
}
.col2 {
  width: 70vw;
  flex: 3;
}
.section h2 {
  margin-left: 15%;
}
.col2 img {
  filter: grayscale();
  width: auto;
  height: 50vh;
  margin-left: 10vw;
  transition-duration: 0.5s;
}

.section:hover img {
  filter: none;
  transform: scale(1.05);
}

.section:hover h2 {
  transform: scale(1.05);
}
.section:last-child {
  margin-bottom: 1rem;
}

@media screen and (max-width: 800px) {
  h1 {
    font-size: 3rem;
  }
  .section {
    flex-direction: column;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 2.5rem;
  }
  .section {
    max-width: 100vw;
  }
  .section h2 {
    margin-left: 0;
    margin: 0 auto;
  }
  .col1 {
    width: 75vw;
  }
  .col2 img {
    height: 25vh;
    margin-top: 5vw;
    margin-left: 15vw;
  }
}
